@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,400;0,500;0,700;1,400&display=swap);
* {
  box-sizing: border-box;
  font-family: "Jost", sans-serif;
  color: #212121;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.6;
}

button {
  border: none;
  cursor: pointer;
}

button:disabled {
  cursor: not-allowed;
}

audio,
video {
  display: inline-block;
}

img {
  border-style: none;
}

.form-control {
  margin-bottom: 2rem;
}

.form-control label,
.form-control input,
.form-control textarea {
  width: 100%;
  display: block;
}

.form-control label {
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.form-control input,
.form-control textarea {
  border: 1px solid #512da8;
  border-radius: 3px;
  font-size: 1.6rem;
  padding: 8px;
}

.form-control input:hover,
.form-control input:active,
.form-control textarea:hover,
.form-control textarea:active {
  border: 1px solid #311b92;
}

.form-actions button,
.btn {
  background: #512da8;
  font-size: 1.6rem;
  border-radius: 5px;
  padding: 0.8rem 2rem;
  margin-right: 1rem;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.25);
  color: #fff;
  cursor: pointer;
}

.form-actions button:hover,
.form-actions button:active,
.btn:hover,
.btn:active {
  background: #311b92;
}

.main-navigation {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background: #512da8;
  padding: 1rem 2.5rem;
  display: flex;
  align-items: center;
}

.main-navigation__logo h1 {
  margin: 0;
  font-size: 3rem;
  color: #fff;
}

.main-navigation__items {
  margin-left: auto;
}

.main-navigation__items ul {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  align-items: center;
}

.main-navigation__items li {
  margin: 0 1rem;
}

.main-navigation__items a,
.main-navigation__items button {
  text-decoration: none;
  color: #fff;
  padding: 5px;
  font-size: 1.6rem;
  background: transparent;
  vertical-align: middle;
  margin: 0;
}

.main-navigation__items a:hover,
.main-navigation__items a:active,
.main-navigation__items a.active,
.main-navigation__items button:hover,
.main-navigation__items button:active {
  background-color: #fff;
  border-radius: 3px;
  color: #311b92;
}

.auth-form {
  width: 60rem;
  max-width: 90%;
  margin: 10rem auto;
}

@media (max-width: 768px) {
  .form-actions {
    display: flex;
    flex-direction: column;
  }

  .form-actions button {
    margin-right: 0;
    margin-bottom: 1rem;
  }
}

.spinner {
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.loader div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: #512da8;
  -webkit-animation: loader 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
          animation: loader 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.loader div:nth-child(1) {
  left: 8px;
  -webkit-animation-delay: -0.24s;
          animation-delay: -0.24s;
}
.loader div:nth-child(2) {
  left: 32px;
  -webkit-animation-delay: -0.12s;
          animation-delay: -0.12s;
}
.loader div:nth-child(3) {
  left: 56px;
  -webkit-animation-delay: 0;
          animation-delay: 0;
}
@-webkit-keyframes loader {
  0% {
    top: 8px;
    height: 64px;
  }
  50%,
  100% {
    top: 24px;
    height: 32px;
  }
}
@keyframes loader {
  0% {
    top: 8px;
    height: 64px;
  }
  50%,
  100% {
    top: 24px;
    height: 32px;
  }
}

.bookings__list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  width: 60rem;
  max-width: 90%;
}

.bookings__item {
  margin: 1rem 0;
  padding: 1rem;
  border: 1px solid #512da8;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bookings__item-data h1,
.bookings__item-data p {
  margin: 0;
}

.bookings__item-data h1 {
  font-size: 2.4rem;
  color: #512da8;
}

.bookings__item-data p {
  font-size: 1.6rem;
}

@media (max-width: 768px) {
  .bookings__item {
    flex-direction: column;
    align-items: flex-start;
  }

  .bookings__item div {
    margin-bottom: 1rem;
  }

  .bookings__item-actions,
  .bookings__item-actions button {
    width: 100%;
  }
}

.booking-chart {
  text-align: center;
  width: 60rem;
  max-width: 95%;
  margin: 0 auto;
}

.bookings__control {
  text-align: center;
  padding: 1rem;
}

.bookings__control button {
  background: transparent;
  color: #512da8;
  padding: 0.8rem 5rem;
  border-bottom: 2px solid transparent;
  font-size: 2rem;
  transition: all 0.2s ease;
}

.bookings__control button.active {
  border-bottom-color: #311b92;
  color: #311b92;
  font-weight: 800;
}

.bookings__control button:focus {
  outline: none;
}

.modal {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  position: fixed;
  top: 10vh;
  width: 90%;
  left: 5%;
  z-index: 100;
}

.modal__header {
  border-radius: 8px 8px 0 0;
  padding: 1rem 2rem;
  background: #512da8;
}

.modal__header h1 {
  margin: 0;
  color: #fff;
}

.modal__content {
  padding: 1rem 2rem;
  font-size: 1.6rem;
}

.modal__content h1,
.modal__content h2,
.modal__content h4 {
  margin: 0;
}

.modal__content h1 {
  font-size: 4rem;
  color: #512da8;
  font-weight: 800;
}

.modal__content h2 {
  font-size: 3rem;
  color: #474747;
}

.modal__content h4 {
  font-size: 2rem;
}

.modal__content p {
  font-size: 1.6rem;
  color: #8f8f8f;
}

.modal__actions {
  display: flex;
  justify-content: flex-end;
  padding: 2rem;
}

@media (min-width: 768px) {
  .modal {
    width: 50rem;
    left: calc((100% - 50rem) / 2);
  }
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.75);
}

.event__list-item {
  margin: 2rem 0;
  padding: 1.2rem;
  border: 1px solid #512da8;
  font-size: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.event__list-item h1 {
  margin: 0;
  font-size: 3rem;
  color: #512da8;
}

.event__list-item h2 {
  margin: 0;
  font-size: 2;
  color: #474747;
}

.event__list-item p {
  margin: 0;
  font-size: 1.6rem;
  color: #8f8f8f;
}

@media (max-width: 768px) {
  .event__list-item {
    flex-direction: column;
    align-items: flex-start;
  }

  .event__list-item div {
    margin-bottom: 1.2rem;
  }

  .event__list-item__control {
    width: 100%;
    text-align: center;
  }

  .event__list-item__control button {
    width: 100%;
  }
}

.event__list {
  width: 80rem;
  max-width: 90%;
  margin: 2rem auto;
  list-style: none;
  padding: 0;
}

.events-control {
  text-align: center;
  border: 1px solid #512da8;
  padding: 2rem;
  margin: 2rem auto;
  width: 60rem;
  max-width: 90%;
}

.main-content {
  margin: 10rem 5rem;
}

@media (max-width: 768px) {
  .main-content {
    margin: 10rem 2rem;
  }
}

